html,
body,
#app {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', Helvetica, Arial, sans-serif; }

.root {
  padding: 1em; }
  .root h2 {
    margin: 0.2em 0;
    padding: 0; }
  .root .flex-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap; }
  .root .button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: 1px solid gray;
    border-radius: 0.5em;
    padding: 0.2em 0.4em; }
  .root .select,
  .root .button {
    font-size: 1.2em;
    padding: 0.2em 0.4em;
    margin-top: 0.4em; }
  .root div.message {
    margin-top: 1em; }

.dropdown {
  margin-right: 1em;
  margin-top: 0.5em; }
  .dropdown .dropdown-content {
    max-height: 400px;
    overflow-y: auto; }
  .dropdown .tag-item {
    min-height: 20px;
    margin: 2px; }
    .dropdown .tag-item > .search {
      padding: 4px 0 2px 2px;
      line-height: 20px;
      display: inline-block; }
  .dropdown .node .fa {
    font-size: 12px;
    margin-left: 4px;
    cursor: pointer; }
    .dropdown .node .fa-globe {
      color: darkorange; }
    .dropdown .node .fa-file-o {
      color: blue; }
    .dropdown .node .fa-copy {
      color: green; }

.dropdown-section {
  border: 1px solid gray;
  border-radius: 0.5em;
  padding: 1em;
  margin: 0.5em 0; }
  .dropdown-section.visible > a:before,
  .dropdown-section.hidden > a:before {
    display: inline-block;
    font-family: monospace;
    font-size: 1.5em;
    font-weight: 800;
    margin-right: 0.3em; }
  .dropdown-section.visible > a:before {
    content: '-'; }
  .dropdown-section.hidden > a:before {
    content: '+'; }
  .dropdown-section > p,
  .dropdown-section > a {
    display: block;
    margin: 0;
    padding: 0.5em 0;
    text-decoration: none; }

.json {
  font-family: Menlo, Monaco, "Courier New", monospace;
  font-weight: normal;
  font-size: 0.9em;
  line-height: 1.2em;
  letter-spacing: 0;
  color: #d4d4d4;
  text-align: left;
  padding: 1em 0;
  margin: 0; }
  .json .json-pretty {
    padding-left: 30px;
    padding-right: 30px; }
  .json .json-selected {
    background-color: rgba(139, 191, 228, 0.2); }
  .json .json-string {
    color: #6caedd; }
  .json .json-key {
    color: #ec5f67; }
  .json .json-boolean {
    color: #99c794; }
  .json .json-number {
    color: #99c794; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

